import "../style/css/loader.css"
import { useSelector } from "react-redux"

const Loader = ({}) => {

    const loader = useSelector((state) => state.loader)

    return(
        <>
            <div className={loader ? "loader-cont" : "hide-loader"}  >
                <div class="lds-dual-ring"></div>
            </div>
        </>
    )
}

export default Loader